export default {
  name: "Collapse",
  props: {
    value: {
      type: [Boolean, String],
      "default": false
    },
    active: {
      type: [Boolean, String],
      "default": true
    },
    transition: {
      type: String,
      "default": 'fadeInLeft'
    },
    classes: {
      type: String,
      "default": 'btn btn-block'
    }
  },
  computed: {
    isActive: function isActive() {
      return this.value === this.active;
    }
  },
  methods: {
    activate: function activate() {
      var _this = this;

      if (this.isActive) {
        this.$emit('input', false);
      } else {
        this.$emit('input', this.active);
      }

      setTimeout(function () {
        return _this.$scrollTo(_this.$refs.title);
      }, 160);
    }
  }
};