      this.$emit('input', this.value ? null : true);
    },
    hasSlot: function hasSlot() {
      var name = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : 'default';
      return !!this.$slots[name] || !!this.$scopedSlots[name];
