export default {
  name: "Page",
  props: {
    slug: {
      type: String,
      required: true
    }
  },
  beforeCreate: function beforeCreate() {
    var _this = this;

    var slug = this.$options.propsData.slug;
    this.$bind.mapGetters('Pages', {
      page: slug
    });
    this.$bind.mapActions('Pages', {
      show: 'show'
    });
    this.$nextTick(function () {
      return _this.show(_this.slug);
    });
  }
};