/** @method logout */

/** @property dropdown */
export default {
  name: "VueRoot",
  beforeCreate: function beforeCreate() {
    this.$bind.mapGetters('Config', {
      environment: 'environment'
    });
    this.$bind.mapGetters('Menus', {
      dropdown: 'dropdown',
      sidebar: 'sidebar'
    });
    this.$bind.mapActions('Auth', {
      logout: 'logout'
    });
    this.$bind.mapGetters('Auth', {
      name: 'entity.name',
      loggedIn: 'entity.id'
    });
    this.$bind.mapState('Auth', {
      $request: '$request',
      $errors: '$errors'
    });
  }
};